import React from 'react';
import { styled } from '@mui/material/styles';
import {
  Box,
  Container,
  Typography,
  Button,
} from '@mui/material';
import { motion } from 'framer-motion';
import identityImg from '../images/identity.jpg';
import webDevImg from '../images/web-dev.webp';
import socialImg from '../images/social-media.webp';
import contentIcon from '../images/influencer-from-south-africa-vlogging-while-self-isolating.webp';
import trainingIcon from '../images/young-women-recording-podcast-in-studio.webp';
import seoIcon from '../images/Logo_Google_Analytics.png';
import coopcomLogo from '../images/logo.png';
import servicesBackgroundImg from '../images/services background.jpg';

const Section = styled(Box)(({ theme }) => ({
  padding: theme.spacing(8, 0),
  background: 'linear-gradient(135deg, #fffbe6 0%, #f5e9da 100%)',
  [theme.breakpoints.down('sm')]: {
    padding: theme.spacing(3, 0),
  },
}));

const GlassySectionHeader = ({ title, description }: { title: string; description: string }) => (
  <Box sx={{ position: 'relative', width: '100%', minHeight: { xs: 340, md: 420 }, display: 'flex', alignItems: 'center', justifyContent: 'center', mb: 8, overflow: 'hidden' }}>
    <Box
      component="img"
      src={servicesBackgroundImg}
      alt="Background"
      sx={{
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        objectFit: 'cover',
        zIndex: 0,
        filter: 'brightness(0.55) blur(1.5px)',
      }}
    />
    <motion.div
      initial={{ opacity: 0, y: 40 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.9, ease: [0.4, 0, 0.2, 1] }}
    >
      <div
        style={{
          background: 'rgba(255,255,255,0.7)',
          boxShadow: '0 8px 32px rgba(44, 62, 80, 0.13)',
          borderRadius: '2.5rem',
          padding: '2.5rem 2rem',
          maxWidth: 700,
          width: '100%',
          position: 'relative',
          textAlign: 'center',
          backdropFilter: 'blur(18px)',
        }}
      >
        <img
          src={coopcomLogo}
          alt="CoopCom Logo"
          style={{ width: 180, height: 180, objectFit: 'contain', marginBottom: 16, opacity: 0.7, display: 'block', marginLeft: 'auto', marginRight: 'auto' }}
        />
        <Typography variant="h2" sx={{ color: '#2d1c10', fontWeight: 800, fontFamily: 'Poppins, Lato, Open Sans, Arial, sans-serif', mb: 2 }}>{title}</Typography>
        <Typography variant="h6" align="center" color="text.secondary" sx={{ mb: 4 }}>
          {description}
        </Typography>
      </div>
    </motion.div>
  </Box>
);

const Services: React.FC = () => {
  const services = [
    {
      "title": "Création d'identité visuelle",
      "description": "Donnez vie à votre marque avec une identité visuelle unique et professionnelle.",
      "image": identityImg,
      "features": [
        "Création de logo",
        "Charte graphique complète",
        "Design de cartes de visite",
        "Templates pour réseaux sociaux"
      ],
      "price": "À partir de 1500€"
    },
    {
      "title": "Développement web",
      "description": "Sites web modernes et performants adaptés à vos besoins.",
      "image": webDevImg,
      "features": [
        "Sites vitrine",
        "E-commerce",
        "Applications web",
        "Optimisation SEO"
      ],
      "price": "À partir de 2000€"
    },
    {
      "title": "Gestion des réseaux sociaux",
      "description": "Stratégie et contenu pour une présence sociale efficace.",
      "image": socialImg,
      "features": [
        "Stratégie de contenu",
        "Création de posts",
        "Community management",
        "Analyse de performance"
      ],
      "price": "À partir de 800€/mois"
    },
    {
      "title": "Création de contenu digital",
      "description": "Photo, vidéo, rédaction pour tous vos supports digitaux.",
      "image": contentIcon,
      "features": [
        "Photographie professionnelle",
        "Production vidéo",
        "Rédaction web",
        "Création de visuels"
      ],
      "price": "Sur devis"
    },
    {
      "title": "Formation & Support digital",
      "description": "Ateliers, accompagnement et support pour maîtriser le digital.",
      "image": trainingIcon,
      "features": [
        "Formations personnalisées",
        "Support technique",
        "Ateliers pratiques",
        "Accompagnement individuel"
      ],
      "price": "Sur devis"
    },
    {
      "title": "SEO & Analytics",
      "description": "Optimisation du référencement et analyse de performance.",
      "image": seoIcon,
      "features": [
        "Audit SEO",
        "Optimisation technique",
        "Suivi des performances",
        "Rapports détaillés"
      ],
      "price": "Sur devis"
    },
    {
      "title": "Développement d'applications mobiles",
      "description": "Applications mobiles sur mesure pour iOS et Android.",
      "image": require("../images/tp-dev-web-web-mobile.jpeg"),
      "features": [
        "Applications natives et hybrides",
        "Design UX/UI mobile",
        "Notifications push",
        "Publication sur stores"
      ],
      "price": "À partir de 3000€"
    },
    {
      "title": "Campagnes publicitaires digitales",
      "description": "Gestion de campagnes Google Ads, Facebook & Instagram Ads.",
      "image": require("../images/Brandwatch_logo.png"),
      "features": [
        "Création de campagnes",
        "Optimisation du budget",
        "Rapports de performance",
        "Ciblage avancé"
      ],
      "price": "À partir de 500€/campagne"
    },
    {
      "title": "Conseil en stratégie digitale",
      "description": "Accompagnement stratégique pour booster votre présence en ligne.",
      "image": require("../images/COOPCOM.jpg"),
      "features": [
        "Audit digital",
        "Plan d'action personnalisé",
        "Veille concurrentielle",
        "Suivi de projet"
      ],
      "price": "Sur devis"
    },
    {
      "title": "Podcast & Webinars",
      "description": "Production et diffusion de podcasts et webinaires professionnels.",
      "image": require("../images/young-women-recording-podcast-in-studio.webp"),
      "features": [
        "Enregistrement audio/vidéo",
        "Montage professionnel",
        "Diffusion multi-plateformes",
        "Animation & modération"
      ],
      "price": "Sur devis"
    }
  ];

  return (
    <>
      <GlassySectionHeader title="Nos Services" description="Des solutions digitales sur mesure pour accompagner la croissance de votre entreprise." />
      <Section sx={{ backgroundColor: '#f5f5f5' }}>
        <Container maxWidth="lg">
          <Box
            sx={{
              display: 'grid',
              gridTemplateColumns: { xs: '1fr', sm: '1fr 1fr', md: '1fr 1fr 1fr' },
              gap: 5,
              mt: 2,
            }}
          >
            {services.map((service, idx) => (
              <Box
                key={idx}
                sx={{
                  background: 'rgba(255,255,255,0.97)',
                  borderRadius: 4,
                  boxShadow: '0 4px 24px rgba(44, 62, 80, 0.10)',
                  overflow: 'hidden',
                  transition: 'transform 0.3s, box-shadow 0.3s',
                  '&:hover': {
                    transform: 'translateY(-8px) scale(1.03)',
                    boxShadow: '0 8px 32px rgba(44, 62, 80, 0.18)',
                  },
                  display: 'flex',
                  flexDirection: 'column',
                }}
              >
                <Box
                  component="img"
                  src={service.image}
                  alt={service.title}
                  sx={{ width: '100%', height: 180, objectFit: 'cover' }}
                />
                <Box sx={{ p: 3, flex: 1, display: 'flex', flexDirection: 'column' }}>
                  <Typography variant="h6" sx={{ fontWeight: 700, mb: 1, color: '#2d1c10' }}>
                    {service.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" sx={{ mb: 2, flex: 1 }}>
                    {service.description}
                  </Typography>
                  <Box sx={{ mb: 2 }}>
                    {service.features.map((feature, fidx) => (
                      <Typography key={fidx} variant="body2" sx={{ display: 'flex', alignItems: 'center', color: '#7a5c3e' }}>
                        • {feature}
                      </Typography>
                    ))}
                  </Box>
                  <Typography variant="subtitle1" sx={{ color: '#ffa726', fontWeight: 700, mt: 'auto' }}>
                    {service.price}
                  </Typography>
                </Box>
              </Box>
            ))}
          </Box>
        </Container>
      </Section>

      <Section>
        <Container maxWidth="lg">
          <Typography variant="h3" component="h2" align="center" gutterBottom>
            Besoin d'une solution personnalisée ?
          </Typography>
          <Typography variant="h6" align="center" color="text.secondary" paragraph>
            Contactez-nous pour discuter de votre projet
          </Typography>
          <Box sx={{ textAlign: 'center', mt: 4 }}>
            <Button
              variant="contained"
              color="primary"
              size="large"
              sx={{ mr: 2 }}
            >
              Nous contacter
            </Button>
            <Button
              variant="outlined"
              color="primary"
              size="large"
            >
              Voir nos réalisations
            </Button>
          </Box>
        </Container>
      </Section>
    </>
  );
};

export default Services; 