import { createTheme } from '@mui/material/styles';
import backgroundImg from './images/background.jpg';
import { CssBaseline } from '@mui/material';

const theme = createTheme({
  palette: {
    primary: {
      main: '#ff9800', // deeper orange for better contrast
      contrastText: '#1a1208', // almost black
    },
    secondary: {
      main: '#2d1c10', // deep brown
      contrastText: '#fffbe6', // light for contrast
    },
    background: {
      default: 'rgba(255,255,255,0.92)', // more opaque for readability
      paper: 'rgba(255,255,255,0.98)',
    },
    text: {
      primary: '#1a1208', // almost black for best readability
      secondary: '#7a5c3e', // warm brown
    },
    warning: {
      main: '#F4A300',
    },
    info: {
      main: '#F9D77E',
    },
  },
  shape: {
    borderRadius: 24, // more modern, rounded corners
  },
  typography: {
    fontFamily: 'Inter, Montserrat, Arial, sans-serif',
    h1: { fontWeight: 800, letterSpacing: 2 },
    h2: { fontWeight: 700, letterSpacing: 1.5 },
    h3: { fontWeight: 700 },
    h4: { fontWeight: 600 },
    h5: { fontWeight: 600 },
    h6: { fontWeight: 500 },
    button: { borderRadius: 24, textTransform: 'none', fontWeight: 700 },
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          borderRadius: 24,
          fontWeight: 700,
          fontSize: '1.1rem',
          boxShadow: '0 4px 24px rgba(244, 163, 0, 0.08)',
        },
        containedPrimary: {
          backgroundColor: '#F4A300',
          color: '#2D1C0B',
          '&:hover': {
            backgroundColor: '#d48c00',
            color: '#fff',
          },
        },
        outlinedPrimary: {
          borderColor: '#F4A300',
          color: '#F4A300',
          '&:hover': {
            backgroundColor: '#FFF8ED',
            borderColor: '#d48c00',
            color: '#d48c00',
          },
        },
      },
    },
    MuiCssBaseline: {
      styleOverrides: {
        body: {
          minHeight: '100vh',
        },
      },
    },
    MuiTypography: {
      styleOverrides: {
        root: {
          textShadow: '0 1px 4px rgba(44,62,80,0.10)',
        },
      },
    },
  },
});

export default theme; 