import React, { useState } from 'react';
import { styled } from '@mui/material/styles';
import {
  Box,
  Container,
  Typography,
  TextField,
  Button,
  Paper,
  Snackbar,
  Alert,
  Card,
  CardContent,
} from '@mui/material';
import { motion } from 'framer-motion';
import coopcomLogo from '../images/logo.png';

const Section = styled(Box)(({ theme }) => ({
  padding: theme.spacing(8, 0),
  background: 'linear-gradient(135deg, #fffbe6 0%, #f5e9da 100%)',
  [theme.breakpoints.down('sm')]: {
    padding: theme.spacing(3, 0),
  },
}));

const ModernCard = styled(Card)(({ theme }) => ({
  background: 'rgba(255,255,255,0.92)',
  borderRadius: 32,
  boxShadow: '0 8px 32px rgba(44, 62, 80, 0.10)',
  padding: theme.spacing(4),
  marginBottom: theme.spacing(4),
  fontFamily: 'Poppins, Lato, Open Sans, Arial, sans-serif',
}));

const ContactForm = styled(Paper)(({ theme }) => ({
  background: 'rgba(255,255,255,0.85)',
  borderRadius: 32,
  boxShadow: '0 12px 40px rgba(255,183,77,0.13)',
  border: '1.5px solid #ffb74d',
  padding: theme.spacing(4),
  height: '100%',
}));

const ContactInfo = styled(Paper)(({ theme }) => ({
  background: '#fffbe6',
  color: '#2d1c10',
  border: '1.5px solid #ffb74d',
  boxShadow: '0 12px 40px rgba(255,183,77,0.13)',
  borderRadius: 32,
  padding: theme.spacing(4),
  height: '100%',
}));

const CenteredCard = styled(Card)(({ theme }) => ({
  background: 'rgba(255,255,255,0.97)',
  borderRadius: 32,
  boxShadow: '0 8px 32px rgba(44, 62, 80, 0.10)',
  padding: theme.spacing(6, 4),
  maxWidth: 700,
  margin: '0 auto',
  textAlign: 'center',
  fontFamily: 'Poppins, Lato, Open Sans, Arial, sans-serif',
}));

interface GlassySectionHeaderProps {
  title: string;
  description: string;
}

const GlassySectionHeader = ({ title, description }: GlassySectionHeaderProps) => (
  <Box
    sx={{
      position: 'relative',
      width: '100%',
      minHeight: { xs: 340, md: 420 },
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      mb: 8,
      overflow: 'hidden',
    }}
  >
    <Box
      component="img"
      src={coopcomLogo}
      alt="Background"
      sx={{
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        objectFit: 'cover',
        zIndex: 0,
        filter: 'brightness(0.55) blur(1.5px)',
      }}
    />
    <Box
      sx={{
        position: 'relative',
        zIndex: 1,
        width: '100%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <motion.div
        initial={{ opacity: 0, y: 40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.9, ease: [0.4, 0, 0.2, 1] }}
      >
        <div
          style={{
            background: 'rgba(255,255,255,0.7)',
            boxShadow: '0 8px 32px rgba(44, 62, 80, 0.13)',
            borderRadius: '2.5rem',
            padding: '2.5rem 2rem',
            maxWidth: 700,
            width: '100%',
            position: 'relative',
            textAlign: 'center',
            backdropFilter: 'blur(18px)',
          }}
        >
          <img
            src={coopcomLogo}
            alt="CoopCom Logo"
            style={{ width: 180, height: 180, objectFit: 'contain', marginBottom: 16, opacity: 0.7, display: 'block', marginLeft: 'auto', marginRight: 'auto' }}
          />
          <Typography variant="h2" sx={{ color: '#2d1c10', fontWeight: 800, fontFamily: 'Poppins, Lato, Open Sans, Arial, sans-serif', mb: 2 }}>{title}</Typography>
          <Typography variant="h6" align="center" color="text.secondary" sx={{ mb: 4 }}>
            {description}
          </Typography>
        </div>
      </motion.div>
    </Box>
  </Box>
);

const ContactCard = styled('div')(({ theme }) => ({
  background: 'rgba(255,255,255,0.97)',
  borderRadius: 32,
  boxShadow: '0 8px 32px rgba(44, 62, 80, 0.10)',
  padding: theme.spacing(5, 4),
  maxWidth: 1100,
  margin: '0 auto',
  display: 'flex',
  flexDirection: 'row',
  gap: theme.spacing(5),
  [theme.breakpoints.down('md')]: {
    flexDirection: 'column',
    gap: theme.spacing(3),
    padding: theme.spacing(3, 1.5),
  },
  [theme.breakpoints.down('sm')]: {
    padding: theme.spacing(2, 0.5),
    gap: theme.spacing(2),
  },
}));

const Contact: React.FC = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: '',
  });

  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success' as 'success' | 'error',
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Here you would typically send the form data to your backend
    console.log('Form submitted:', formData);
    setSnackbar({
      open: true,
      message: 'Votre message a été envoyé avec succès !',
      severity: 'success',
    });
    setFormData({
      name: '',
      email: '',
      subject: '',
      message: '',
    });
  };

  const handleCloseSnackbar = () => {
    setSnackbar((prev) => ({ ...prev, open: false }));
  };

  return (
    <>
      <GlassySectionHeader title="Contactez-nous" description="Contactez-nous pour discuter de vos besoins en communication digitale et découvrir nos solutions stratégiques." />
      <Section>
        <Container maxWidth="lg">
          <ContactCard>
            <Box sx={{ flex: 1, minWidth: 260 }}>
              <ContactInfo elevation={3} sx={{ background: '#fffbe6', color: '#2d1c10', border: '1.5px solid #ffb74d', boxShadow: '0 12px 40px rgba(255,183,77,0.13)' }}>
                <Typography variant="h4" gutterBottom>
                  Nos coordonnées
                </Typography>
                <Box sx={{ mb: 3 }}>
                  <Typography variant="h6" gutterBottom>
                    Adresse
                  </Typography>
                  <Typography variant="body1">
                    Avenue Abdelkrim El Khattabi<br />
                    93000 Tétouan, Maroc
                  </Typography>
                </Box>
                <Box sx={{ mb: 3 }}>
                  <Typography variant="h6" gutterBottom>
                    Téléphone
                  </Typography>
                  <Typography variant="body1">
                    +212 23 23 45 67 89
                  </Typography>
                </Box>
                <Box sx={{ mb: 3 }}>
                  <Typography variant="h6" gutterBottom>
                    Email
                  </Typography>
                  <Typography variant="body1">
                    contact@coopcom.fr
                  </Typography>
                </Box>
                <Box>
                  <Typography variant="h6" gutterBottom>
                    Horaires d'ouverture
                  </Typography>
                  <Typography variant="body1">
                    Lundi - Vendredi: 9h - 18h<br />
                    Samedi: 10h - 16h<br />
                    Dimanche: Fermé
                  </Typography>
                </Box>
              </ContactInfo>
            </Box>
            <Box sx={{ flex: 2, minWidth: 300 }}>
              <ContactForm elevation={3} sx={{ background: 'rgba(255,255,255,0.85)', border: '1.5px solid #ffb74d', boxShadow: '0 12px 40px rgba(255,183,77,0.13)' }}>
                <form onSubmit={handleSubmit}>
                  <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 3, '& > *': { width: { xs: '100%', sm: 'calc(50% - 12px)' } } }}>
                    <Box sx={{ width: { xs: '100%', sm: 'calc(50% - 12px)' } }}>
                      <TextField
                        required
                        fullWidth
                        label="Nom"
                        value={formData.name}
                        onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                      />
                    </Box>
                    <Box sx={{ width: { xs: '100%', sm: 'calc(50% - 12px)' } }}>
                      <TextField
                        required
                        fullWidth
                        label="Email"
                        type="email"
                        value={formData.email}
                        onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                      />
                    </Box>
                    <Box sx={{ width: '100%' }}>
                      <TextField
                        required
                        fullWidth
                        label="Sujet"
                        value={formData.subject}
                        onChange={(e) => setFormData({ ...formData, subject: e.target.value })}
                      />
                    </Box>
                    <Box sx={{ width: '100%' }}>
                      <TextField
                        required
                        fullWidth
                        multiline
                        rows={4}
                        label="Message"
                        value={formData.message}
                        onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                      />
                    </Box>
                    <Box sx={{ width: '100%' }}>
                      <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        size="large"
                        fullWidth
                      >
                        Envoyer
                      </Button>
                    </Box>
                  </Box>
                </form>
              </ContactForm>
            </Box>
          </ContactCard>
        </Container>
      </Section>

      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={handleCloseSnackbar}
      >
        <Alert
          onClose={handleCloseSnackbar}
          severity={snackbar.severity}
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </>
  );
};

export default Contact; 