import React, { useState } from 'react';
import { styled } from '@mui/material/styles';
import { AppBar, Toolbar, Typography, Button, Container, Box, IconButton, Drawer, List, ListItem, ListItemText, ListItemButton } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Link as RouterLink } from 'react-router-dom';
import logo from '../images/logo.png';

const StyledAppBar = styled(AppBar)(({ theme }) => ({
  backgroundColor: '#fff',
  color: theme.palette.text.primary,
  boxShadow: '0 4px 24px rgba(44, 62, 80, 0.08)',
  position: 'fixed',
  top: 0,
  width: '100%',
  zIndex: 1100,
}));

const StyledToolbar = styled(Toolbar)(({ theme }) => ({
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  minHeight: 40,
  padding: '0 2rem',
  background: '#fff',
}));

const NavButton = styled(Button)(({ theme }) => ({
  color: theme.palette.text.primary,
  marginLeft: theme.spacing(3),
  fontWeight: 600,
  fontSize: '1.1rem',
  borderRadius: 10,
  padding: '5px 24px',
  transition: 'background 0.2s, color 0.2s',
  '&:hover': {
    backgroundColor: theme.palette.primary.main,
    color: '#fff',
  },
}));

const Footer = styled('footer')(({ theme }) => ({
  backgroundColor: '#f5f5f5',
  padding: theme.spacing(4, 0),
  marginTop: 'auto',
}));

interface MainLayoutProps {
  children: React.ReactNode;
}

interface GlassySectionHeaderProps {
  title: string;
  subtitle: string;
}

const MainLayout: React.FC<MainLayoutProps> = ({ children }) => {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const navLinks = [
    { label: 'Accueil', to: '/' },
    { label: 'À propos', to: '/about' },
    { label: 'Services', to: '/services' },
    { label: 'Réalisations', to: '/portfolio' },
    { label: 'Blog', to: '/blog' },
    { label: 'Contact', to: '/contact' },
  ];
  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <StyledAppBar position="static">
        <Container maxWidth="lg" sx={{ py: 1 }}>
          <StyledToolbar sx={{ position: 'relative', minHeight: 40, px: 0 }}>
            {/* Logo: left on desktop, centered on mobile */}
            <Box
              sx={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: { xs: 'center', md: 'flex-start' },
                flex: { xs: 1, md: 'none' },
                width: { xs: '100%', md: 120 },
                position: { xs: 'absolute', md: 'static' },
                left: { xs: 0, md: 'auto' },
                right: { xs: 0, md: 'auto' },
                mx: { xs: 'auto', md: 0 },
                zIndex: 2,
                height: 40,
              }}
            >
              <Box sx={{ height: { xs: 56, md: 80 }, width: { xs: 56, md: 80 }, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <img
                  src={logo}
                  alt="CoopCom Logo"
                  style={{ height: '100%', width: '100%', objectFit: 'contain', display: 'block', margin: '0 auto' }}
                />
              </Box>
            </Box>
            {/* Nav links for desktop */}
            <Box sx={{ display: { xs: 'none', md: 'flex' }, alignItems: 'center', height: 40, marginLeft: 'auto' }}>
              {navLinks.map((link) => (
                <RouterLink key={link.to} to={link.to} style={{ textDecoration: 'none' }}>
                  <NavButton>{link.label}</NavButton>
                </RouterLink>
              ))}
            </Box>
            {/* Hamburger menu for mobile (right) */}
            <IconButton
              edge="end"
              color="inherit"
              aria-label="menu"
              onClick={() => setDrawerOpen(true)}
              sx={{ display: { xs: 'flex', md: 'none' }, position: 'absolute', right: 8 }}
            >
              <MenuIcon fontSize="large" />
            </IconButton>
            {/* Drawer for mobile nav */}
            <Drawer anchor="right" open={drawerOpen} onClose={() => setDrawerOpen(false)}>
              <Box sx={{ width: 220, pt: 2 }} role="presentation" onClick={() => setDrawerOpen(false)}>
                <List>
                  {navLinks.map((link) => (
                    <ListItem key={link.to} disablePadding>
                      <ListItemButton component={RouterLink} to={link.to}>
                        <ListItemText primary={link.label} />
                      </ListItemButton>
                    </ListItem>
                  ))}
                </List>
              </Box>
            </Drawer>
          </StyledToolbar>
        </Container>
      </StyledAppBar>

      <Box component="main" sx={{ flexGrow: 1, paddingTop: { xs: '4rem', md: '6rem' } }}>
        {children}
      </Box>

      <Footer>
        <Container maxWidth="lg">
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="body2" color="text.secondary">
              © 2024 CoopCom. Tous droits réservés.
            </Typography>
            <Box>
              <RouterLink to="/mentions-legales" style={{ textDecoration: 'none' }}>
                <NavButton>Mentions légales</NavButton>
              </RouterLink>
              <RouterLink to="/politique-confidentialite" style={{ textDecoration: 'none' }}>
                <NavButton>Politique de confidentialité</NavButton>
              </RouterLink>
            </Box>
          </Box>
        </Container>
      </Footer>
    </Box>
  );
};

export default MainLayout; 